import { PropsWithChildren, useState } from "react"
import { BigdataChatAvatar } from "@rp/react/common/assets"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, Typography } from "@rp/react/web/mui/core"
import { CHAT_SCROLL_TOP_OFFSET } from "../../scrollToLastInteraction"
import { getTextMargin } from "../getTextMargin"

type Props = PropsWithChildren<{
  isChatEmpty: boolean
}>

const ChatSuggestions = ({ children, isChatEmpty }: Props) => {
  const { isMobile } = useDeviceInfo()
  const [textRef, setTextRef] = useState<HTMLDivElement | null>(null)

  return (
    <Box
      sx={{
        minHeight: isChatEmpty ? `calc(100dvh - ${CHAT_SCROLL_TOP_OFFSET}px)` : 0,
        mx: isMobile ? 0 : 7,
      }}
    >
      <Box
        sx={{
          display: "flex",
          gap: isMobile ? 1 : 2,
          flexWrap: isMobile ? "wrap" : undefined,
          mx: isMobile ? 2 : 0,
        }}
      >
        <Box>
          <BigdataChatAvatar height={isMobile ? 28 : 40} width={isMobile ? 28 : 40} />
        </Box>
        {isMobile ? (
          <Typography sx={{ mt: 0.5, fontSize: "body2.fontSize", color: "text.secondary" }}>
            Bigdata
          </Typography>
        ) : null}
        <Typography
          sx={{
            fontSize: isMobile ? 18 : 17,
            lineHeight: 1.7,
            mt: isMobile ? 0 : getTextMargin(textRef),
            width: "100%",
          }}
          component="div"
          ref={setTextRef}
        >
          {children}
        </Typography>
      </Box>
    </Box>
  )
}

export { ChatSuggestions }
